.App {
  display: flex;
  flex-direction: column;
  font-family: Arial, sans-serif;
}

input {
  font-family: Arial, sans-serif;
  font-size: 15px;
}

.message-bubble {
  border-radius: 10px;
  background-color: rgba(193, 193, 239, 0.52);
  padding: 8px;
  margin: 7px;
  align-self: flex-end;
  max-width: 60%;
}

.ai-message-bubble {
  background-color: rgba(193, 239, 227, 0.52);
  align-self: flex-start;
}

.messages {
  display: flex;
  flex-direction: column;
  height: 80vh;
}

.chat-field {
  align-self: flex-end;
}

.chat-field-input {
  padding: 8px;
  margin-right: 7px;
  border-radius: 10px;
}

.send-chat-button {
  border: none;
  border-radius: 10px;
  padding: 8px;
  background-color: rgb(14, 14, 14);
  color: white;
  font-size: 14px;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.dots {
  width: 50px;
  height: 20px;
  border-radius: 10px;
  background-color: rgba(193, 239, 227, 0.52);
  margin: 10px;
  transform: scale(0);
  animation: overall-scale 8s infinite;
}

.dot {
  animation-timing-function: ease-in;
  animation-iteration-count: infinite;
  animation-name: dot-scale;
  animation-duration: 1s;

  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 5px 0px;
  border-radius: 10px;
  background-color: #383636;
  transform: scale(0.65);

  &:first-of-type {
    margin-left: 6px;
  }
  &:nth-of-type(2) {
    animation-delay: 0.15s;
  }
  &:nth-of-type(3) {
    animation-delay: 0.3s;
  }
}

@keyframes dot-scale {
  0%,
  70% {
    transform: scale(0.65);
  }
  35% {
    transform: scale(1);
  }
}

@keyframes overall-scale {
  0%,
  95% {
    transform: scale(0);
  }
  5%,
  90% {
    transform: scale(1);
  }
}
